@font-face {
  font-family: roboto;
  src: url(../);
}
.wrapper {
  display: grid;
  margin: auto;
  grid-template-columns: 100%;
  grid-template-rows: 133px fit-content(1400px);
}

#line {
  background-color: #EB5F59;
  width: 100%;
  height: 20px;
}

#welcome-area {
  padding: 20px 40px 30px 40px;
  background-color: #233D4D;
  line-height: 21px;
}

#logo {
  display: block;
  margin: auto;
  margin-top: 25px;
}

h1 {
  text-align: center;
  color: white !important;
  font-size: 32px !important;
}

#welcome-area {
  color: white;
}

p {
  text-align: center;
  font-size: 18px !important;
}

h2 {
  text-align: center;
  padding-top: 50px !important;
  margin-bottom: 30px !important;
  color: #233D4D;
}

#courses {
  margin: auto;
  background-image: linear-gradient(#EBF4E1, #E2F3E1, #E4F4F1, #FFFFFF);
}

.row {
  justify-content: center;
  column-gap: 20px;
  row-gap: 20px;
  margin-bottom: 50px;
}

.row > * {
  padding-left: 0 !important;
}

.row-cols-2 > * {
  width: 145px !important;
  height: 145px !important;
}

.button-course {
  width: 9rem;
  height: 9rem;
  border-radius: 20px;
  border: 1px solid #233D4D;
  background-color: white;
  box-shadow: 4px 4px 5px rgb(197, 197, 197);
}

.button-course[name=one]:active {
  border: 10px solid #A1BF80;
  box-shadow: 2px 2px 2px rgb(197, 197, 197);
}

.button-course[name=two]:active {
  border: 10px solid #FBC945;
  box-shadow: 2px 2px 2px rgb(197, 197, 197);
}

