img {
  height: 70px !important;
  margin: 10px 10px 0px 10px;
}

#button-privacy {
  border-radius: 30px;
  background-color: #EBF4E1;
  padding: 9px 25px 9px 25px;
  border: none;
  display: block;
  margin: auto;
  margin-top: 20px;
  font-weight: bold;
  text-decoration: none;
}

#privacy-text {
  color: #233D4D;
  font-size: 11pt !important;
  line-height: 12pt;
  width: 70%;
  display: block;
  margin: auto;
  margin-bottom: 50px;
  margin-top: 70px;
}